      reward: [], // 基地防御報酬(外部JSON)
      experience: [], // 経験値テーブル(外部JSON)
      needs: [
       { No: 1, NextLv: 0, NeedBattle: 0, NeedCredit: 0, NeedCore: 0, ArrivalDate: "", NeedYen: 0, HourBattle: 0, HourCredit: 0, HourCore: 0, DayBattle: 0, DayCredit: 0, DayCore: 0, DayBattlePer: 0, DayCreditPer: 0, DayCorePer: 0, LogBattle: '', LogCredit: '', LogCore: '', ShowLog: 0 }
      ,{ No: 2, NextLv: 0, NeedBattle: 0, NeedCredit: 0, NeedCore: 0, ArrivalDate: "", NeedYen: 0, HourBattle: 0, HourCredit: 0, HourCore: 0, DayBattle: 0, DayCredit: 0, DayCore: 0, DayBattlePer: 0, DayCreditPer: 0, DayCorePer: 0, LogBattle: '', LogCredit: '', LogCore: '', ShowLog: 0 }
      ,{ No: 3, NextLv: 0, NeedBattle: 0, NeedCredit: 0, NeedCore: 0, ArrivalDate: "", NeedYen: 0, HourBattle: 0, HourCredit: 0, HourCore: 0, DayBattle: 0, DayCredit: 0, DayCore: 0, DayBattlePer: 0, DayCreditPer: 0, DayCorePer: 0, LogBattle: '', LogCredit: '', LogCore: '', ShowLog: 0 }
      ], // 必要値

