  mounted(){
    let _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく
    let smZIndexCnt = 1;
    let drag = null;      
    let dragPre = null;

    //ソートの矢印が表示されるよう起動時にNoを昇順ソート
    this.mSortBy('id', 'check');

    const asyncFunc = async () => { // 外部データ読み込み
      //const startTime = performance.now();
      let divisionsURL = "https://raw.githubusercontent.com/saito885/NikkeBill/main/json/divisions.json";
      let contentsURL = "https://raw.githubusercontent.com/saito885/NikkeBill/main/json/contents.json";
      let rewardURL = "https://raw.githubusercontent.com/saito885/NikkeBill/main/json/reward.json";
      let experienceURL = "https://raw.githubusercontent.com/saito885/NikkeBill/main/json/experience.json";
      let packsURL = "https://raw.githubusercontent.com/saito885/NikkeBill/main/json/packs.json";
      let nikkesURL = "https://raw.githubusercontent.com/saito885/NikkeBill/main/json/nikkes.json";
      let updatesURL = "https://raw.githubusercontent.com/saito885/NikkeBill/main/json/updates.json";
      await axios.get(divisionsURL).then(function(response) {_this.divisions = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(contentsURL).then(function(response) {_this.contents = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(rewardURL).then(function(response) {_this.reward = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(experienceURL).then(function(response) {_this.experience = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(packsURL).then(function(response) {_this.packs = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(nikkesURL).then(function(response) {_this.nikkes = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(updatesURL).then(function(response) {_this.updates = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      //const endTime = performance.now();
      //console.log(endTime - startTime);
    };

    function moveNikke(cls, e, drag) {
      let dummy = document.getElementById("sfDrag");
      let t;
      if (cls == 1) {
        t = e;
      } else {
        t = e.changedTouches[0];
      }
      if(document.documentElement.clientWidth <= 520 && _this.coSelTab == 3) {
        _this.sqDummyImgSize = 60;
      } else if(document.documentElement.clientWidth <= 520) {
        _this.sqDummyImgSize = 40;
      } else if(document.documentElement.clientWidth <= 960) {
        _this.sqDummyImgSize = 60;
      } else {
        _this.sqDummyImgSize = 80;
      }
      _this.sqDummyImgName = drag.name
      dummy.style.top = t.pageY - 40 + "px";
      dummy.style.left = t.pageX - 40 + "px";
      dummy.style.display = "";
      if (_this.coSelTab == 4) {
        dummy.style.borderRadius = "100px";
      } else {
        dummy.style.borderRadius = "";
      }
    }

    function dropNikke(cls, e, drag) {
      // ドラック用ダミー画像を非表示にする
      let dummy = document.getElementById("sfDrag");
      dummy.style.display = "none";

      if (drag != undefined && drag.id.includes('nikke')) { // タッチしていたものがニケ画像の場合のみ処理する

        // ダブルタップ判定
        if (cls == 2) {
          //console.log("ダブルタップ判定")
          //console.log("drag.id=" + drag.id)
          if (drag == dragPre) { // 前回タップした要素と同じ要素の場合はダブルタップとみなす
            if (drag.parentNode.parentNode.id.startsWith('squad')) {
              //console.log("ダブルタップ1")
              _this.mNikkeDbl(drag.id, 'squad', 'candidate'); // 部隊編成でのダブルタップイベント
            } else {
              //console.log("ダブルタップ2")
              _this.mNikkeDbl(drag.id, 'smGraph', 'smCandidate'); // 散布図でのダブルタップイベント
            }
          } else {
            dragPre = drag; // タップした要素を記録
            //console.log("dragPre.id=" + dragPre.id)
            setTimeout(function () {dragPre = null;}, 5000); // 500ms以内にタップされなければタップ記録をクリア
          }
        }

        // ドロップ先
        let t = null;
        let dropPoint = null;
        if (cls == 1) {
          dropPoint = document.elementFromPoint(e.clientX, e.clientY);
        } else {
          t = e.changedTouches[0]; // モバイル端末でタップした位置のうち1つ目(ドロップ先)
          dropPoint = document.elementFromPoint(t.clientX, t.clientY);
          //drag = e.target; // ドロップ先
          //drag.style.position = ""; // ？
        }
        let drop = dropPoint;
        //let dstNikkeTop = 0;
        //let dstNikkeLeft = 0;
        
        // ドロップ先がニケ画像ならドロップ先をニケ画像の父・祖父に置き換え
        if (drop.id.includes('nikke') || drop.id == 'markBurst') {
          drop = drop.parentNode.parentNode;
          //dstNikkeTop = Number(drop.style.top.replace('px',''));
          //dstNikkeLeft = Number(drop.style.left.replace('px',''));
        }

        if (drop.id.startsWith('smGraph')) {
          //let dropRect = drop.getBoundingClientRect(); // ドロップ先の要素自身の座標
          let dropRect = document.getElementById(drop.id).parentNode.getBoundingClientRect(); // ドロップ先要素の座標
          let dragRect = document.getElementById(drag.id).parentNode.getBoundingClientRect(); // ニケ画像の座標

          //let graph = document.getElementById('smGraph');
          drop.appendChild(drag.parentNode);

          //target.style.top = dropRect.top + e.offsetY + dstNikkeTop - targetRect.height/2*0.6 + "px";
          //target.style.left = dropRect.left + e.offsetX + dstNikkeLeft - targetRect.width/2*0.6 + "px";
          //drag.parentNode.style.top = e.clientY + dstNikkeTop - targetRect.height/2 + "px";
          //drag.parentNode.style.left = e.clientX + dstNikkeLeft - targetRect.width/2 + "px";
          drag.style.left = "0px";
          drag.parentNode.style.top = (e.offsetY - dropRect.top - dragRect.height/2) + "px";
          drag.parentNode.style.left = (e.offsetX - dropRect.left - dragRect.width/2) + "px";
          drag.parentNode.style.position = "absolute";
          drag.parentNode.style.zIndex = ++smZIndexCnt;
          //console.log("----------------------------");
          //console.log("drag=" + drag.id);
          //console.log("drop=" + drop.id);
          //console.log("ドロップ先要素自身 Top/Left=" + dropRect.top + "/" + dropRect.left);
          //console.log("ドロップ画像 Y/X=" + e.clientY + "/" + e.clientY);
          //console.log("ドロップ画像 Y/X=" + e.offsetY + "/" + e.offsetX);
          //console.log("ドロップ先 Y/X=" + dropRect.top + "/" + dropRect.left);
          //console.log("ニケ画像 縦/横=" + dragRect.height + "/" + dragRect.width);
          //console.log("設置場所 Y/X=" + drag.parentNode.style.top + "/" + drag.parentNode.style.left);
        
        } else if (drop.id == 'candidate' || drop.id == 'smCandidate') {
          let idx = 0; //drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
          let cnt = 0;
          // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
          drop.childNodes.forEach(function(child){
            child.childNodes.forEach(function(grandchild){
              if(grandchild.id != undefined && grandchild.id.includes('nikke')) {
                if(grandchild.id <= drag.id) idx = cnt+1;
                cnt++;
              }
            });
          });

          drop.insertBefore(drag.parentNode, drop.childNodes.item(idx)); // ドロップ先画像の1つ前に追加

          // ドロップした画像のstyleを元に戻す
          drag.parentNode.style.top = "0px";
          drag.parentNode.style.left = "0px";
          drag.parentNode.style.zIndex = 1;
          drag.parentNode.style.position = "relative";

          if (drop.id == 'candidate') _this.mSaveQf(_this.sqSquadSelect);

        } else {
          // ドロップ先が部隊欄
          if (drop.id.startsWith('squad')) {
            let idxTo = drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
            let cnt = 0;
            // ドロップ先の画像が部隊の枠の中の何番目の要素なのか探す
            drop.childNodes.forEach(function(child){
              child.childNodes.forEach(function(grandchild){
                if(grandchild.id != undefined && grandchild.id.includes('nikke')) {
                  if(grandchild.id == dropPoint.id) {
                    idxTo = cnt;
                  }
                  cnt++;
                }
              });
            });

            // ドロップ元と先が同一部隊内の場合、ドロップ元の要素番号を探す
            let idxFrom = 0; // ドロップ元の要素番号
            cnt = 0;
            if(drop.id == drag.parentNode.parentNode.id) {
              drop.childNodes.forEach(function(child){
                child.childNodes.forEach(function(grandchild){
                  if(grandchild.id != undefined && grandchild.id.includes('nikke')) {
                    if(grandchild.id == drag.id) idxFrom = cnt;
                    cnt++;
                  }
                });
              });
              if(idxFrom < idxTo) { // ドロップ元要素番号<ドロップ先要素番号の場合、挿入先をドロップ先要素の後ろにする
                idxTo++;
              }
            }
            //console.log("drag.id=" + drag.id);
            //console.log("drop.id=" + drop.id);
            //console.log("idxFrom=" + idxFrom);
            //console.log("idxTo=" + idxTo);
            drop.insertBefore(drag.parentNode, drop.childNodes.item(idxTo)); // ドロップ先画像を挿入
          }

          _this.mSaveQf(_this.sqSquadSelect);
          // 部隊編成の部隊欄をクッキーへ保存（何故かmSaveQfメソッドを使うと$cookies.setが出来ないので直書き）
          // ★類似メソッドmSaveQfがあるため修正時はそちらも見直す事
          //let buf = "";
          //for (let i = 1; i <= 6; ++i) {
          //  let squad = document.getElementById("squad"+i);
          //  squad.childNodes.forEach(function(child){
          //    child.childNodes.forEach(function(grandchild){
          //      //console.log(grandchild.name);
          //      if(grandchild.name != undefined) buf = buf + grandchild.name + ",";
          //    });
          //  });
          //  _this.$cookies.set('squad'+i+"_"+_this.sqSquadSelect, buf, 0);
          //};
        };

        drag.style.display = ''; // 表示状態に戻す
        drag = null;
      }
    }

    function afterJsonRead() { // 外部データ読み込み後処理
      let nikkeView = [];
      let nikkeGraph = [];

      _this.nikkes.forEach(function(nikke){ // ニケ画像にドラッグ関連イベントを付与
        nikkeView[nikke.num] = document.getElementById(nikke.id);
        nikkeGraph["sm" + nikke.num] = document.getElementById("sm" + nikke.id);
        
        nikkeView[nikke.num].addEventListener('dragstart', function(e){ // ドラッグが開始された時
          drag = e.target;
        }, false);
        
        nikkeView[nikke.num].addEventListener('drag', function(e){ // ドラッグ中 
          //nikkeView[nikke.num].style.display = 'none';
          moveNikke(1, e, drag);
        }, false);
        
        nikkeView[nikke.num].addEventListener('touchstart', function(e){ // タッチ面に指が触れた時
          drag = e.target;
        }, false);

        nikkeView[nikke.num].addEventListener('touchmove', function(e){ // タッチしながら指を移動させた時
          //e.preventDefault();
          moveNikke(2, e, drag);
        }, false);

        nikkeGraph["sm" + nikke.num].addEventListener('dragstart', function(e){ // ドラッグが開始された時
          drag = e.target;
        }, false);

        nikkeGraph["sm" + nikke.num].addEventListener('drag', function(e){ // ドラッグ中 
          //nikkeView[nikke.num].style.display = 'none';
          moveNikke(1, e, drag);
        }, false);

        nikkeGraph["sm" + nikke.num].addEventListener('touchstart', function(e){ // タッチ面に指が触れた時
          drag = e.target;
        }, false);

        nikkeGraph["sm" + nikke.num].addEventListener('touchmove', function(e){ // タッチしながら指を移動させた時
          //e.preventDefault();
          moveNikke(2, e, drag);
        }, false);
      })

      document.addEventListener('dragover', function(e){ // ドロップ可能エリア内にある時
        e.preventDefault(); // 操作のキャンセル
      }, false);
          
      document.addEventListener('drop', function(e){ // ドロップした時
        e.preventDefault(); // 操作のキャンセル
        dropNikke(1, e, drag);
      }, false);
    
      document.addEventListener('touchend', function(e){ // タッチしていた指を離した時
        dropNikke(2, e, drag);

        if (false && e.target.id.includes('nikke')) { // タッチしていたものがニケ画像の場合のみ処理する
          drag = e.target; // ドロップ先
          drag.style.position = "";

          // ドラック用ダミー画像を非表示にする
          let dummy = document.getElementById("sfDrag");
          dummy.style.display = "none";

          if (drag == dragPre) { // 前回タップした要素と同じ要素の場合はダブルタップとみなす
            if (drag.parentNode.parentNode.id.startsWith('squad')) {
              _this.mNikkeDbl(drag.id, 'squad', 'candidate'); // 部隊編成でのダブルタップイベント
            } else {
              _this.mNikkeDbl(drag.id, 'smGraph', 'smCandidate'); // 散布図でのダブルタップイベント
            }
          } else {

            dragPre = drag; // タップした要素を記録
            setTimeout(function () {dragPre = null;}, 500); // 500ms以内にタップされなければタップ記録をクリア

            let t = e.changedTouches[0];
            let dropPoint = document.elementFromPoint(t.clientX, t.clientY);
            let drop = null;
            let dstNikkeTop = 0;
            let dstNikkeLeft = 0;

            // ドロップ先がニケ画像ならドロップ先をニケ画像の祖父（部隊欄1～6または散布図グラフ）に置き換え
            if (dropPoint.id.startsWith('nikke') || dropPoint.id == 'markBurst') {
              drop = dropPoint.parentNode.parentNode;
            } else if (dropPoint.id.startsWith('smnikke')) {
              dstNikkeTop = Number(e.target.style.top.replace('px',''));
              dstNikkeLeft = Number(e.target.style.left.replace('px',''));
              drop = dropPoint.parentNode;
            } else {
              drop = dropPoint;
            }

            if (drop.id.startsWith('squad')) { // ドロップ先が部隊欄
              let idxTo = drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
              let cnt = 0;

              // ドロップ先の画像が部隊の枠の中の何番目の要素なのか探す
              drop.childNodes.forEach(function(child){
                child.childNodes.forEach(function(grandchild){
                  if(grandchild.id != undefined && grandchild.id.includes('nikke')) {
                    if(grandchild.id == dropPoint.id) idxTo = cnt;
                    cnt++;
                  }
                });
              });

              // ドロップ元と先が同一部隊内の場合、ドロップ元の要素番号を探す
              let idxFrom = 0; // ドロップ元の要素番号
              cnt = 0;
              if(drop.id == drag.parentNode.parentNode.id) {
                drop.childNodes.forEach(function(child){
                  child.childNodes.forEach(function(grandchild){
                    if(grandchild.id != undefined && grandchild.id.includes('nikke')) {
                      if(grandchild.id == drag.id) idxFrom = cnt;
                      cnt++;
                    }
                  });
                });
                if(idxFrom < idxTo) { // ドロップ元要素番号<ドロップ先要素番号の場合、挿入先をドロップ先要素の後ろにする
                  idxTo++;
                }
              }

              drop.insertBefore(drag.parentNode, drop.childNodes.item(idxTo)); // ドロップ先画像を挿入
            
            } else if (drop.id.startsWith('smGraph')) { // ドロップ先が散布図のグラフ
              let graph = document.getElementById('smGraph');
              let graphRect = document.getElementById(graph.id).getBoundingClientRect(); // グラフの座標
              let targetRect = document.getElementById(drag.id).getBoundingClientRect(); // ニケ画像の座標
              graph.appendChild(drag);
              drag.style.top = t.clientY - graphRect.top - targetRect.height/2 + "px";
              drag.style.left = t.clientX - graphRect.left - targetRect.width/2 + "px";
              drag.style.position = "absolute";
              drag.style.zIndex = ++smZIndexCnt;
                         
            } if (drop.id == 'candidate') { // ドロップ先が部隊編成の候補一覧
              let idx = 0; //drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
              let cnt = 0;
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              drop.childNodes.forEach(function(child){
                child.childNodes.forEach(function(grandchild){
                  if(grandchild.id != undefined && grandchild.id.includes('nikke')) {
                    if(grandchild.id <= drag.id) idx = cnt+1;
                    cnt++;
                  }
                });
              });
              drop.insertBefore(drag.parentNode, drop.childNodes.item(idx)); // ドロップ先画像の1つ前に追加

            } else if (drop.id == 'smCandidate') { // ドロップ先が散布図の候補一覧
              let idx = 0; //drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
              let cnt = 0;
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              drop.childNodes.forEach(function(item){
                  if(item.id <= drag.id) {
                    idx = cnt+1;
                  }
                  cnt++;
                });
              drop.insertBefore(drag, drop.childNodes.item(idx)); // ドロップ先画像の1つ前に追加
              // ドロップした画像のstyleを元に戻す
              drag.style.top = "0px";
              drag.style.left = "0px";
              drag.style.zIndex = 1;
              drag.style.position = "relative";
            }

            drag.style.display = ''; // 表示状態に戻す
            drag = null;

            // 部隊編成の部隊欄をクッキーへ保存（何故かmSaveQfメソッドを使うと$cookies.setが出来ないので直書き）
            // ★類似メソッドmSaveQfがあるため修正時はそちらも見直す事
            let buf = "";
            for (let i = 1; i <= 6; ++i) {
              let squad = document.getElementById("squad"+i);
              squad.childNodes.forEach(function(child){
                child.childNodes.forEach(function(grandchild){
                  //console.log(grandchild.name);
                  if(grandchild.name != undefined) buf = buf + grandchild.name + ",";
                });
              });
              _this.$cookies.set('squad'+i+"_"+_this.sqSquadSelect, buf, 0);
            };
          };
        };
      }, false);

      {
        // 初期選択タブ設定
        _this.coSelTab = _this.$cookies.get('coSelectTab');
        if (_this.coSelTab == null) {
          document.getElementById("TAB-01").checked = true;
          _this.$cookies.set('coSelectTab', 1, 0);
          _this.coSelTab = 1;
        } else {
          let eleTab = document.getElementById("TAB-0" + _this.coSelTab);
          eleTab.checked = true;
        }

        // クッキーから設定読み込み
        _this.coNikkeLv      = _this.$cookies.get('coNikkeLv');
        _this.coRewardLv     = _this.$cookies.get('coRewardLv');
        _this.coRewardCredit = _this.$cookies.get('coRewardCredit');
        _this.coRewardBattle = _this.$cookies.get('coRewardBattle');
        _this.coRewardCore   = _this.$cookies.get('coRewardCore');
        if(_this.coNikkeLv == null)      _this.coNikkeLv = 200;
        if(_this.coRewardLv == null)     _this.coRewardLv = 200;
        if(_this.coRewardCredit == null) _this.coRewardCredit = 37792;
        if(_this.coRewardBattle == null) _this.coRewardBattle = 374405;
        if(_this.coRewardCore == null)   _this.coRewardCore = 57.17;

        _this.lsWipeouts      = _this.$cookies.get('lsWipeouts');
        _this.lsPosCaseCredit = _this.$cookies.get('lsPosCaseCredit');
        _this.lsPosCredit     = _this.$cookies.get('lsPosCredit');
        _this.lsPosCaseBattle = _this.$cookies.get('lsPosCaseBattle');
        _this.lsPosBattle     = _this.$cookies.get('lsPosBattle');
        _this.lsPosCaseCore   = _this.$cookies.get('lsPosCaseCore');
        _this.lsPosCore       = _this.$cookies.get('lsPosCore');
        if(_this.lsWipeouts == null)      _this.lsWipeouts = 1;
        if(_this.lsPosCaseCredit == null) _this.lsPosCaseCredit = 0;
        if(_this.lsPosCredit == null)     _this.lsPosCredit = 0;
        if(_this.lsPosCaseBattle == null) _this.lsPosCaseBattle = 0;
        if(_this.lsPosBattle == null)     _this.lsPosBattle = 0;
        if(_this.lsPosCaseCore == null)   _this.lsPosCaseCore = 0;
        if(_this.lsPosCore == null)       _this.lsPosCore = 0;

        _this.pJewel = _this.$cookies.get('pJewel');
