    mSaveQf(tag) { // クッキーへ部隊欄を保存
      let buf = "";
      for (let i = 1; i <= 6; ++i) {
      let squad = document.getElementById("squad"+i);
        squad.childNodes.forEach(function(child){
          child.childNodes.forEach(function(grandchild){
            if(grandchild.id != undefined && grandchild.id.includes('nikke')) {
              buf = buf + grandchild.name + ",";
            }
          });
        });
        this.$cookies.set('squad'+i+"_"+tag, buf, 0);
      };
    },

    mLoadQf(tag) { // クッキーから部隊欄を読み取り再現する
      this.sqSquadSelect = tag;
      this.mSfClearNikke(false);
      let buf = "";
      let menbers = [];
      for (let i = 1; i <= 6; ++i) {
        buf = this.$cookies.get('squad'+i+"_"+tag);
        //console.log('squad'+i+"_"+tag);
        //console.log(buf);
        let squad = document.getElementById("squad" + i);
        if (buf) {
          menbers = buf.split(",");
          menbers.forEach(function(menber){
            candidate.childNodes.forEach(function(child){
              child.childNodes.forEach(function(grandchild){
                if(grandchild.id != undefined && grandchild.id.includes("nikke")) {
                  if (grandchild.name == menber) squad.appendChild(grandchild.parentNode);
                }
              });
            });
          });
        }
      }
    },

    mSfClearNikke(flg) { // 部隊編成の部隊欄から候補一覧へニケを戻す
      let ret = true;
      if (flg) {
        ret = window.confirm('部隊の配置をクリアしますが宜しいですか？');
      }

      if (ret) {
        let candidate = document.getElementById("candidate");
        let memberId = null;
        for (let i = 1; i <= 6; ++i) {
          let squad = document.getElementById("squad" + i);
          while (squad.childNodes.length != 0) {
            squad.childNodes.forEach(function(member){
              member.childNodes.forEach(function(item){
                if(item.id != undefined && item.id.includes("nikke")) memberId = item.id;
              });
              
              let idx = 0; // ドロップ先の要素番号
              let cnt = 0;
              //console.log("memberId=" + memberId)
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              candidate.childNodes.forEach(function(child){
                child.childNodes.forEach(function(grandchild){
                  if(grandchild.id != undefined && grandchild.id.includes("nikke")) {
                    //console.log("grandchild.id=" + grandchild.id)
                    if(grandchild.id <= memberId) idx = cnt+1;
                    cnt++;
                  }
                });
              });
              candidate.insertBefore(member, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加
            });
          };
        };
        if (flg) {
          this.mSaveQf(this.sqSquadSelect);
        }
      };
    },

    mSmClearNikke(msg) { // 散布図メーカーのグラフから候補一覧へニケを戻す
      let ret = false;
      if (msg != "") {
        ret = window.confirm(msg);
      }

      if (ret) {
        let candidate = document.getElementById("smCandidate");
        let graph = document.getElementById("smGraph");
        let memberId = null;
        let flgFind = true;
        while (flgFind) {
          flgFind = false;
          graph.childNodes.forEach(function(member){
            memberId = null;
            member.childNodes.forEach(function(item){
              if(item.id != undefined && item.id.includes("nikke")) memberId = item.id;
            });

            if (memberId == null) { // idがnullの要素(上下左右の表題)はスキップ
            } else if (memberId.includes("nikke")) {
              flgFind = true;
              let idx = 0; // ドロップ先の要素番号
              let cnt = 0;
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              candidate.childNodes.forEach(function(child){
                child.childNodes.forEach(function(grandchild){
                  if(grandchild.id != undefined && grandchild.id.includes("nikke")) {
                    if(grandchild.id <= memberId) idx = cnt+1;
                    cnt++;
                  }
                });
              });
              candidate.insertBefore(member, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加

              // ドロップした画像のstyleを元に戻す
              member.style.top = "0px";
              member.style.left = "0px";
              member.style.zIndex = 1;
              member.style.position = "relative";
            };
          });
        };
        //console.log("graph.childNodes.length=" + graph.childNodes.length)
      };
      return ret;
    },

