      this.sfDispBurst = !this.sfDispBurst;
    },

    mSkillInfoClose(skillId, nikkeId) { // 部隊編成 - スキル詳細を消す
      //console.log("--------------------------------")
      //console.log("skillId=" + skillId)
      //console.log("nikkeId=" + nikkeId)
      //let target = document.getElementById(skillInfoId);
      //target.parentNode.parentNode.style.display = "none"; // スキル詳細を非表示化
      
      // 絞り込み対象スキル情報の配列から×ボタンが押されたものを消す
      let cnt = 0;
      let idx = 0;
      this.skillInfos.forEach(function(skillInfo){
