          if(_this.qfFil_S_OH_SHI) {
            let reg = /(遮蔽物)/;
            if(reg.test(nikke.skill1) || reg.test(nikke.skill2) || reg.test(nikke.burst)) {
              if(reg.test(nikke.skill1)) skill1 = skill1.replace(reg, '<mark>$1</mark>');
              if(reg.test(nikke.skill2)) skill2 = skill2.replace(reg, '<mark>$1</mark>');
              if(reg.test(nikke.burst))  burst  = burst.replace(reg, '<mark>$1</mark>');
              flgFind = true;
            }
          }
          if(_this.qfFil_S_OH_DEBLIF) {
            let reg = /(デバフ効果解除)/;
            if(reg.test(nikke.skill1) || reg.test(nikke.skill2) || reg.test(nikke.burst)) {
              if(reg.test(nikke.skill1)) skill1 = skill1.replace(reg, '<mark>$1</mark>');
              if(reg.test(nikke.skill2)) skill2 = skill2.replace(reg, '<mark>$1</mark>');
              if(reg.test(nikke.burst))  burst  = burst.replace(reg, '<mark>$1</mark>');
              flgFind = true;
            }
          }
          if(keyword.length >= 2 || keyword[0] != '') {
            if (flgFind) { // AND検索にするためここまでで表示対象と判定されたNIKKEのみを検索とする
              flgFind = false; // AND検索にするため一旦falseにする
              for (let cnt = 0; cnt < keyword.length; cnt++) {
                let reg = new RegExp("(" + keyword[cnt] + ")");
                if((reg.test(nikke.skill1)) && (skill1.includes('<mark>'))) {
                  skill1 = skill1.replace(reg, '<mark>$1</mark>');
                  flgFind = true;
                }
                if((reg.test(nikke.skill2)) && (skill2.includes('<mark>'))) {
                  skill2 = skill2.replace(reg, '<mark>$1</mark>');
                  flgFind = true;
                }
                if((reg.test(nikke.burst)) && (burst.includes('<mark>'))) {
                  burst = burst.replace(reg, '<mark>$1</mark>');
                  flgFind = true;
                }
              }
            };
          };
          if(target.length >= 2 || target[0] != '') {
            if (flgFind) { // AND検索にするためここまでで表示対象と判定されたNIKKEのみを検索とする
              flgFind = false; // AND検索にするため一旦falseにする
              for (let cnt = 0; cnt < target.length; cnt++) {
                let reg = new RegExp("(" + target[cnt] + ")");
                if((reg.test(nikke.skill1)) && (skill1.includes('<mark>'))) {
                  skill1 = skill1.replace(reg, '<mark>$1</mark>');
                  flgFind = true;
                }
                if((reg.test(nikke.skill2)) && (skill2.includes('<mark>'))) {
                  skill2 = skill2.replace(reg, '<mark>$1</mark>');
                  flgFind = true;
                }
                if((reg.test(nikke.burst)) && (burst.includes('<mark>'))) {
                  burst = burst.replace(reg, '<mark>$1</mark>');
                  flgFind = true;
                }
              }
            };
          };
        
          if(flgFind) {
            let reg = /(<mark>)/i;
            if(!reg.test(skill1)) skill1 = "";
            if(!reg.test(skill2)) skill2 = "";
            if(!reg.test(burst)) burst = "";
            //alert(_this.skillInfos.length)
            _this.skillInfos.push({ id: _this.skillInfos.length, nikkeId: nikke.id, name: skillInfoName, skill1: skill1.replace(regRet, "<br>"), skill2: skill2.replace(regRet, "<br>"), burst: burst.replace(regRet, "<br>") });
          } else {
            disp = 'none';
          }

        } else if (((keyword.length >= 2 || keyword[0] != '') || (target.length >= 2 || target[0] != '')) && (disp != 'none')) {
          let flgFindKey = false;
          let flgFindTag = false;
          let regRet = /\n/g;
          let skillInfoName = nikke.name+"【バースト"+nikke.burstclass+"／"+nikke.class+"／"+nikke.weapon+"／"+nikke.elemental+"】";
          let skill1 = nikke.skill1;
          let skill2 = nikke.skill2;
          let burst  = nikke.burst;

          for (let cnt = 0; cnt < keyword.length; cnt++) {
            let reg = new RegExp("(" + keyword[cnt] + ")");
            if (reg.test(nikke.skill1)) {
              skill1 = skill1.replace(reg, '<mark>$1</mark>');
              flgFindKey = true;
            }
            if (reg.test(nikke.skill2)) {
              skill2 = skill2.replace(reg, '<mark>$1</mark>');
              flgFindKey = true;
            }
            if (reg.test(nikke.burst)) {
              burst = burst.replace(reg, '<mark>$1</mark>');
              flgFindKey = true;
            }
          }
          if (keyword.length == 0) {
            flgFindKey = true;
          }

          for (let cnt = 0; cnt < target.length; cnt++) {
            let reg = new RegExp("(" + target[cnt] + ")");
            if (reg.test(nikke.skill1)) {
              skill1 = skill1.replace(reg, '<mark>$1</mark>');
              flgFindTag = true;
            }
            if (reg.test(nikke.skill2)) {
              skill2 = skill2.replace(reg, '<mark>$1</mark>');
              flgFindTag = true;
            }
            if (reg.test(nikke.burst)) {
              burst = burst.replace(reg, '<mark>$1</mark>');
              flgFindTag = true;
            }
          }
          if (target.length == 0) {
            flgFindTag = true;
          }
          
          if(flgFindKey && flgFindTag) {
            let reg = /(<mark>)/i;
            if(!reg.test(skill1)) skill1 = "";
            if(!reg.test(skill2)) skill2 = "";
            if(!reg.test(burst)) burst = "";
            _this.skillInfos.push({ id: _this.skillInfos.length, nikkeId: nikke.id, name: skillInfoName, skill1: skill1.replace(regRet, "<br>"), skill2: skill2.replace(regRet, "<br>"), burst: burst.replace(regRet, "<br>") });
          } else {
            disp = 'none';
          }
        }

        if (_this.coSelTab == 4) {
          if (document.getElementById("sm"+nikke.id).parentNode.parentNode.id.startsWith('smGraph')) disp = ''; // グラフに配置されたニケは強制表示
          document.getElementById("sm"+nikke.id).parentNode.style.display = disp;
        } else {
          if (document.getElementById(nikke.id).parentNode.parentNode.id.startsWith('squad')) disp = ''; // 部隊欄に配置されたニケは強制表示
          document.getElementById(nikke.id).parentNode.style.display = disp;
        }        
      })
    },

