    mNikkeDbl(id, parentId, toId) { // 部隊作成でニケ画像をダブルクリック
      let target = document.getElementById(id);
      let candidate = document.getElementById(toId);
      let idx = 0; //candidate.childNodes.length; // ドロップ先の要素番号（デフォルトは先頭）
      let cnt = 0;

      // 部隊欄上に画像がある場合は画像を候補へ移す
      if (target.parentNode.parentNode.id.includes(parentId)) {
        // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
        candidate.childNodes.forEach(function(child){
          child.childNodes.forEach(function(grandchild){
            //console.log("grandchild.id:" + grandchild.id)
            if(grandchild.id != undefined && grandchild.id.includes("nikke")) {
              //console.log("id:" + grandchild.id +" "+ target.id)
              if(grandchild.id <= target.id) {
                idx = cnt+1;
                //console.log("idx:" + idx);
              }
              cnt++;
            };
          });
        });
        //console.log(idx);
        //console.log(target.parentNode.parentNode.id);
        candidate.insertBefore(target.parentNode, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加
        //console.log(target.parentNode.parentNode.id);
        
        // styleを元に戻す
        target.parentNode.style.top = "0px";
        target.parentNode.style.left = "0px";
        target.parentNode.style.zIndex = 1;
        target.parentNode.style.position = "relative";
        
        if (this.coSelTab == 3) {
          this.mSaveQf(this.sqSquadSelect); // 部隊セット保存
        }
      }
    },

